@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
.nav-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 50%;
}

.nav-item {
  font-size: 20pt;
  padding: 2px;
}

.nav-item:hover {
  cursor: pointer;
  text-shadow: 2px 2px 3px #000000be;
  transition: text-shadow 0.2s ease-in-out;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.name {
  font-size: 32pt;
  margin-bottom: 15px;
}

.title {
  font-size: 20pt;
  margin-bottom: 40px;
}

.bio > * {
  font-size: 14pt;
  margin: 15px 0px;
  line-height: normal;
}

.skills {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 75%;
  flex-wrap: wrap;
}

.skill-image {
  height: 60px;
  width: 60px;
}

.skill {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 15px;
}

.skill-name {
  margin: 15px 0px;
}

.project-image {
  width: 400px;
  height: 400px;
  display: block;
  margin: 50px auto;
  border: 3px solid #000000;
}

.project-name {
  font-size: 18pt;
}

.project > * {
  margin-top: 5px;
}

.project-links {
  margin-bottom: 30px;
}

.project-link {
  margin-right: 20px;
}

.contents {
  width: 75%;
  margin: 100px auto;
}

.app {
  margin: 10% 0%;
  font-family: "Roboto Slab", serif;
}

.contents {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* CSS Reset */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

